<template>
  <div id="view-station" class="wrapper">
    <div class="container">
      <h3 class="title is-3">Chi tiết trạm</h3>
      <div>
        <l-map :zoom="zoom" :center="center" style="height: 500px; width: 100%">
          <l-tile-layer :url="url" :attribution="attribution" />
          <l-marker :lat-lng="marker" />
        </l-map>
      </div>
      <div>
        Tên trạm :
        <b>{{ station_name }}</b>
      </div>
      <div>
        Mã trạm :
        <b>{{ station_id }}</b>
      </div>
      <div>
        Địa chỉ :
        <b>{{ station_addr }}</b>
      </div>
      <div>
        Tọa độ :
        <b>{{ station_geo }}</b>
      </div>
      <div>
        Số điện thoại liên lạc :
        <b>{{ station_phone }}</b>
      </div>

      <div class="buttons view-btns">
        <router-link to="/" class="back-btn">
          <b-button type="is-dark">Quay lại</b-button>
        </router-link>
        <router-link
          class="back-btn"
          :to="{
            name: 'edit-station',
            params: { station_id: station_id }
          }"
        >
          <b-button type="is-link">Sửa</b-button>
        </router-link>

        <b-button type="is-danger" @click="deleteStation">Xóa</b-button>
      </div>
    </div>
  </div>
</template>
<script>
import db from "./firebaseInit";
import { latLng } from "leaflet";
import { LMap, LTileLayer, LMarker, LIconDefault } from "vue2-leaflet";
import { Icon } from "leaflet";

delete Icon.Default.prototype._getIconUrl;
Icon.Default.mergeOptions({
  iconRetinaUrl: require("leaflet/dist/images/marker-icon-2x.png"),
  iconUrl: require("leaflet/dist/images/marker-icon.png"),
  shadowUrl: require("leaflet/dist/images/marker-shadow.png")
});
export default {
  name: "view-station",
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LIconDefault
  },
  data() {
    return {
      station_id: null,
      station_name: null,
      station_geo: null,
      station_addr: null,
      station_phone: null,
      zoom: 13,
      center: [21.05056, 105.91631],
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      attribution:
        '&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors',
      marker: latLng(21.05056, 105.91631)
    };
  },
  beforeRouteEnter(to, from, next) {
    db.collection("stations")
      .where("station_id", "==", to.params.station_id)
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            let centerPoint = doc
              .data()
              .station_geo.split(",")
              .map(Number);

            let markerPoint = {
              lat: centerPoint[0],
              lng: centerPoint[1]
            };

            vm.center = centerPoint;
            vm.marker = markerPoint;
            vm.station_id = doc.data().station_id;
            vm.station_name = doc.data().station_name;
            vm.station_geo = doc.data().station_geo;
            vm.station_addr = doc.data().station_addr;
            vm.station_phone = doc.data().station_phone;
          });
        });
      });
  },
  watch: {
    $route: "fetchData"
  },
  methods: {
    fetchData() {
      console.log("fetchData ");
      console.log(this.$route);
      db.collection("stations")
        .where("station_id", "==", this.$route.params.station_id)
        .get()
        .then(querySnapshot => {
          // console.log("queerr2 ");
          // console.log(querySnapshot);
          querySnapshot.forEach(doc => {
            let centerPoint = doc
              .data()
              .station_geo.split(",")
              .map(Number);

            let markerPoint = {
              lat: centerPoint[0],
              lng: centerPoint[1]
            };
            this.center = centerPoint;
            this.marker = markerPoint;
            this.station_id = doc.data().station_id;
            this.station_name = doc.data().station_name;
            this.station_geo = doc.data().station_geo;
            this.station_addr = doc.data().station_addr;
            this.station_phone = doc.data().station_phone;
          });
        });
    },
    deleteStation() {
      this.$buefy.dialog.confirm({
        message: "Bạn chắc chắn xóa chứ?",
        onConfirm: () => {
          db.collection("stations")
            .where("station_id", "==", this.$route.params.station_id)
            .get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                doc.ref.delete();
                this.$router.push("/");
              });
            });
          this.$buefy.toast.open("Đã xóa");
        }
      });
      // if (confirm("Bạn có chắc là muốn xóa trạm này không ?")) {
      //   db.collection("stations")
      //     .where("station_id", "==", this.$route.params.station_id)
      //     .get()
      //     .then(querySnapshot => {
      //       querySnapshot.forEach(doc => {
      //         doc.ref.delete();
      //         this.$router.push("/");
      //       });
      //     });
      // }
    }
  }
};
</script>



